*{
  margin:0;
  padding: 0;
  box-sizing: border-box;
}

html,body{
  width: 100%;
  height: 100vh;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
h2{
  text-align: center;
}
.persona p, .mascota p{
  text-align: left;
}
.persona{
  width: 20rem;
 
}

.mascota{
  width: 18rem;
}
.lista{
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  padding: 0 50px;
}

